    height: 100%;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
    background: #000; }

/* Main image in popup */
img.mfp-img {
  width: auto;
  max-width: 80%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
